
import {
  defineComponent, watch, Teleport, Transition
} from 'vue';

export default defineComponent({
  emits: ['close', ],
  props: {
    modalOpen: {
      type: Boolean,
      required: true,
    },
  },
  setup(props, { emit, }) {
    const closeModal = () => {
      emit('close');
    };

    // prevent page scrolling when the modal is open
    watch(
      () => props.modalOpen,
      (value) => {
        if (value) {
          document.body.style.overflow = 'hidden';
        } else {
          document.body.style.overflow = '';
        }
      }
    );

    return {
      closeModal,
    };
  },
});
